import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('@/components/Home')
const Dashboard = () => import('@/views/Dashboard')
const BaseTable = () => import('@/views/BaseTable')
const Tabs = () => import('@/views/Tabs')
const BaseForm = () => import('@/views/BaseForm')
const VueEditor = () => import('@/views/VueEditor')
const Markdowm = () => import('@/views/Markdowm')
const Upload = () => import('@/views/Upload')
const Icon = () => import('@/views/icon')
const BaseCharts = () => import('@/views/BaseCharts')
const DragList = () => import('@/views/DragList')
const DragDialog = () => import('@/views/DragDialog')
const i18n = () => import('@/views/i18n')
const Permission = () => import('@/views/Permission')
const NoFound403 = () => import('@/views/403')
const NoFound404 = () => import('@/views/404')
Vue.use(VueRouter)

  const routes = [
    {
      path: '/',
      redirect: '/dashboard'
    },
    {
      path: '/',
      component: Home,
      meta: {title: '自述文件'},
      children: [
        {
          path: '/dashboard',
          component: Dashboard,
          meta: { title: '系统首页'}
        },
        {
          path: '/icon',
          component: Icon,
          meta: { title: '自定义图标'}
        },
        {
          path: '/table',
          component: BaseTable,
          meta: { title: '基础表格'}
        },
        {
          path: '/tabs',
          component: Tabs,
          meta: { title: 'tab选项卡'}
        },
        {
          path: '/form',
          component: BaseForm,
          meta: { title: '基本表单'}
        },
        {
          path: '/editor',
          component: VueEditor,
          meta: { title: '富文本编辑器'}
        },
        {
          path: '/markdown',
          component: Markdowm,
          meta: { title: 'markdown编辑器'}
        },
        {
          path: '/charts',
          component: BaseCharts,
          meta: { title: 'schart图表'}
        },
        {
          path: '/upload',
          component: Upload,
          meta: { title: '文件上传'}
        },
        {
          path: '/drag',
          component: DragList,
          meta: { title: '拖拽列表'}
        },
        {
          path: '/dialog',
          component: DragDialog,
          meta: { title: '拖拽弹框'}
        },
        {
          path: '/i18n',
          component: i18n,
          meta: { title: '国际化'}
        },
        {
          path: '/permission',
          component: Permission,
          meta: { title: '权限测试', permission: true}
        },
        {
          path: '/404',
          component: NoFound404,
          meta: { title: '404'}
        },
        {
          path: '/403',
          component: NoFound403,
          meta: { title: '403'}
        },
        {
          path: '/donate',
          component: Dashboard,
          meta: { title: '支持作者'}
        }
      ]
    },
    {
      path: '/login',
      component: () => import('@/views/Login'),
      mate: { title: '登录' }
    },
    {
      path: '*',
      redirect: '/404',
      meta: { title: '404'}
    }
  ]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  document.title = `${to.meta.title} | vue-manage-system`
  const role = localStorage.getItem('ms_username')
  if(!role && to.path !== '/login') {
    console.log('1111');
    next('/login')
  } else if (to.meta.permission) {
    role === 'admin' ? next() : next('/403')
  } else {
    if(navigator.userAgent.indexOf('MSIE') > -1 && to.path === '/editor') {
      Vue.prototype.$alert('vue-quill-editor组件不兼容IE10及以下浏览器，请使用更高版本的浏览器查看', '浏览器不兼容通知', {
        confirmButtonText: '确定'
      })
    } else {
      next()
    }
  } 
})

export default router
